<template>
  <section class="level-change pb-5">
      <b-container class="pt-3 pb-2">
          <small class="text-muted text-uppercase">Для перехода на уровень {{ getLevel() }} необходимо</small>
      </b-container>
      <div class="bg-white pt-3 pb-3">
        <b-container>
            <div class="mb-3">{{ fetchedData.condition }}</div>
            <b-button variant="primary" :disabled="!fetchedData.isAvailable" @click="goToNextLevel">
                Перейти на уровень
            </b-button>
            <div class="h4 mt-3 mb-0 text-danger" v-if="!fetchedData.isAvailable">
                Недоступно. <br>
                {{ fetchedData.unavailableReason }}
            </div>
        </b-container>
      </div>
      <b-container class="pt-4 pb-2">
          <small class="text-muted text-uppercase">Для перехода на уровень {{ getLevel() }} необходимо</small>
      </b-container>
      <div class="bg-white pt-3 pb-3">
          <b-container>
              <div class="mb-3">{{ fetchedData.condition }}</div>
              <b-button variant="primary" class="mb-3" @click="buyNextLevel">
                  Приобрести за {{ fetchedData.price }} BnC
              </b-button>
              <div class="h4 mb-0 text-muted">
                  Всего на счету: {{ fetchedData.balance }} BnC
              </div>
          </b-container>
      </div>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'LevelChange',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/teams/level-change',
      useParamsId: true,
      fetchedData: {
        condition: "",
        isAvailable: false,
        unavailableReason: "",
        price: null,
        balance: null,
      },
      goToNextLevelUrl: '/api/post-success',
      buyNextLevelUrl: '/api/post-success',
    }
  },
  methods: {
    configureHeader() {
      this.$route.meta.title = `Переход на уровень ${this.getLevel()}`;
    },
    getLevel() {
      return this.$route.params.id;
    },
    goToNextLevel() {
      let url = this.goToNextLevelUrl;
      this.axios.post(url, this.form)
        .then(response => {
          this.$router.back();
        })
        .catch(e => {
          this.errors.push(e);
        });
    },
    buyNextLevel() {
      let url = this.goToNextLevelUrl;
      this.axios.post(url, this.form)
        .then(response => {
          this.$router.back();
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  beforeMount() {
    this.fetchUrl = '/api/teams/level-change';
  },
  mounted() {
    const level = this.getLevel();
    const maxLevel = this.$store.getters['global/maxLevel'];

    if (level > maxLevel || level < 1) {
      this.$router.go(-1);
    }
    this.configureHeader();
  }
}
</script>

<style lang="scss">

</style>


