<template>
  <section class="levels pb-5">
      <b-container class="pt-3 pb-3">
        Уровень партнерства определяет условия получения дохода от вашей сети
      </b-container>
      <b-container class="mb-4">
        <div class="levels__doc bg-white d-flex pl-4 pr-4 pt-3 pb-3 rounded">
            <div class="mr-3 mt-2">
                <c-icon name="doc"/>
            </div>
           <div>
               <a :href="fetchedData.document.url" class="text-info">{{ fetchedData.document.name }}</a> <br>
               <small class="text-muted text-uppercase">от {{ fetchedData.document.date }}</small>
           </div>
        </div>
      </b-container>
      <div class="bg-white pt-4 pb-3 border-bottom" v-for="(level, index) in fetchedData.levels" :class="{'mb-2': index !== fetchedData.levels.length - 1}">
          <b-container>
              <div class="d-flex justify-content-between mb-3 pb-1">
                  <div class="h3 m-0 strong mr-2">Уровень {{ level.num }}</div>
                  <div class="text-right">
                      <img :src="getLevelImage(level.num)">
                      <c-icon name="money-indicator" :class="'level-'+level.num"/>
                  </div>
              </div>
              <div class="d-flex justify-content-between h4 mb-2">
                  <div class="text-muted m-0 mr-2">Доход от&nbsp;личных продаж</div>
                  <div class="text-right">{{ level.personalIncome }}</div>
              </div>
              <div class="d-flex justify-content-between h4 mb-2">
                  <div class="text-muted m-0 mr-2">Ваша&nbsp;команда</div>
                  <div class="text-right">{{ level.teamInfo }}</div>
              </div>
              <div class="d-flex justify-content-between h4 mb-2">
                  <div class="text-muted m-0 mr-2">Лично-командный доход</div>
                  <div class="text-right">{{ level.teamIncome }}</div>
              </div>
              <div class="d-flex justify-content-between h4 mb-4">
                  <div class="text-muted m-0 mr-2">Инвест-кэшбэк от&nbsp;Лично-командного дохода в&nbsp;BnC</div>
                  <div class="text-right">{{ level.cashback }}</div>
              </div>
              <router-link :to="{ name: 'TeamsLevelChange', params: { id: level.num } }" v-if="!isCurrentLevel(level.num)">
                  <b-button variant="primary" class="mt-1 mb-3">
                      Перейти на уровень
                  </b-button>
              </router-link>
             <b-button variant="primary" class="mt-1 mb-3" disabled v-if="isCurrentLevel(level.num)">
                 <c-icon name="check"/>
                 Ваш текущий уровень
             </b-button>
          </b-container>
      </div>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'Levels',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/teams/levels',
      fetchedData: {
        document: {},
        level: null,
        levels: [],
      },
    }
  },
  methods: {
    getLevelImage(level) {
     return require(`../../../assets/img/people-indicator-${level}.png`);
    },
    isCurrentLevel(level) {
      return level === this.fetchedData.level;
    }
  },
  mounted() {

  }
}
</script>

<style lang="scss">
    .levels {
        &__doc {
            box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.1);
        }

        .level-1 {
            color: #43d849;
        }

        .level-2 {
            color: #9ed71d;
        }

        .level-3 {
            color: #f58914;
        }

        .level-4 {
            color: #ff2424;
        }
    }
</style>


