import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const global = {
    namespaced: true,
    state: {
        isAuth: false,
        settings: {
          userName: '',
          position: '',
          balance: null,
          hasNews: false,
          maxLevel: null
        }
    },
    mutations: {
        setIsAuth(state, value) {
            state.isAuth = value;
        },
        setSettings(state, value) {
          state.settings = value;
        }
    },
    getters: {
        userName(state) {
          return state.settings.userName;
        },
        position(state) {
          return state.settings.position;
        },
        balance(state) {
          return state.settings.balance;
        },
        hasNews(state) {
          return state.settings.hasNews;
        },
        maxLevel(state) {
          return state.settings.maxLevel;
        }
    }
};

const sidebar = {
    namespaced: true,
    state: {
        isActive: false,
    },
    mutations: {
        toggle(state) {
            state.isActive = !state.isActive;
        },
        hide(state) {
            state.isActive = false;
        },
        show(state) {
            state.isActive = true;
        }
    }
};

const store = new Vuex.Store({
    modules: { global, sidebar }
});

export default {
    store,
    install (Vue, options) {
        Vue.prototype.$store = store
    }
}


